.ImageCroper {
  
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 99;
  
  .button_container {
    position: absolute;
    bottom: 10%;
    left: 0;
    text-align: center;
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .button_close {
    padding: 8px 15px;
    border-radius: 12px;
    background-color: #f7f7f7;
    font-size: 12px;
  }

  .button_set {
    padding: 8px 15px;
    border-radius: 12px;
    background-color: #00bfff;
    color: white;
    font-size: 12px;
    margin-left: 10px;
  }

